.profiletimeline {
  position: relative;
  padding-left: 40px;
  margin: 10px 10px 0 30px;
  border-left: 1px solid #e9ecef;
}
.profiletimeline .sl-left {
  float: left;
  margin-left: -60px;
  z-index: 1;
  margin-right: 15px;
}
.profiletimeline .sl-left img {
  max-width: 40px;
}

.profiletimeline .sl-item {
  margin-top: 8px;
  margin-bottom: 30px;
}

.profiletimeline .sl-date {
  font-size: 12px;
  color: #99abb4;
}

/*******************/
/*Steam line widget*/
/*******************/
.steamline {
  position: relative;
  border-left: 1px solid #e9ecef;
  margin-left: 20px;
}
.steamline .sl-left {
  float: left;
  margin-left: -20px;
  z-index: 1;
  width: 40px;
  line-height: 40px;
  text-align: center;
  height: 40px;
  border-radius: 100%;
  color: #fff;
  margin-right: 15px;
}
.steamline .sl-left img {
  max-width: 40px;
}

.steamline .sl-right {
  padding-left: 30px;
}
.steamline .sl-right .desc,
.steamline .sl-right .inline-photos {
  margin-bottom: 21px;
}

.steamline .sl-item {
  border-bottom: 1px solid #e9ecef;
  margin: 20px 0;
}
.steamline .sl-item:last-child {
  border-bottom: none;
}

.sl-date {
  font-size: 10px;
  color: #99abb4;
}

.time-item {
  border-color: #e9ecef;
  padding-bottom: 1px;
  position: relative;
}
.time-item:before {
  content: ' ';
  display: table;
}
.time-item:after {
  background-color: #fff;
  border-color: #e9ecef;
  border-radius: 10px;
  border-style: solid;
  border-width: 2px;
  bottom: 0;
  content: '';
  height: 14px;
  left: 0;
  margin-left: -8px;
  position: absolute;
  top: 5px;
  width: 14px;
}

.time-item-item:after {
  content: ' ';
  display: table;
}

.item-info {
  margin-bottom: 15px;
  margin-left: 15px;
}
.item-info p {
  margin-bottom: 10px !important;
}

