}

.button-comprar-carrito-fav {
  border: 1px solid var(--naranja-abi);
  color: var(--naranja-abi);
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 5px;
  background-color: #fff;
}

.icono-add {
  color: var(--gris-claro-abi);
}
.icono-add:hover {
  color: green;
}
.icono-remove {
  color: var(--gris-claro-abi);
}
.icono-remove:hover {
  color: var(--rojo-abi);
}

.toggle-green {
  --background: #efefef;
  --background-checked: #efefef;
  --handle-background-checked: #28991f;
}

.footer-carousel-productos {
  color: var(--rojo-abi);
  padding: 10px 0;
  margin-bottom: 5px;
  cursor: pointer;
  font-size: 0.9rem;
}

