  .button-comprar-carrito-fav {
    color: var(--naranja-abi);
    padding: 5px;
    border-radius: 5px;
    background-color: #fff;
  }

  .footer-vista-app,
  .footer-app {
    background-color: #efefef;
  }
}

@media screen and (min-width: 501px) {
  .button-comprar-carrito-fav {
    border: 1px solid var(--naranja-abi);
    color: var(--naranja-abi);
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 5px;
    background-color: #fff;
  }
}

/* Remove default checkbox */
.form-group [type='checkbox']:not(:checked),
.form-group [type='checkbox']:checked {
  position: absolute;
  left: -9999px;
  opacity: 0;
}

.cpointer,
a {
  cursor: pointer;
}

.page-wrapper .container-lg {
  padding: 15px;
}

.swiper-container-horizontal > .swiper-scrollbar {
  height: 3px !important;
}

#cantidad-agregar,
.cantidad-agregar {
  position: absolute;
}

#cantidad-agregar:before,
.cantidad-agregar:before {
  content: attr(data-cantidadagregar);
  display: block;
}

.sendtocart {
  display: block;
  animation: xAxis 0.5s forwards cubic-bezier(0.165, 0.84, 0.44, 1);
}

.sendtocart:before {
  animation: yAxis 0.5s alternate forwards cubic-bezier(1, 0.44, 0.84, 0.165);
}

#icon-carrito.shake {
  animation: shakeCart 0.4s ease-in-out forwards;
}

@keyframes xAxis {
  100% {
    transform: translateX(calc(125px));
  }
}

@keyframes yAxis {
  100% {
    transform: translateY(calc(-50vh));
  }
}

@keyframes shakeCart {
  25% {
    transform: translateX(6px);
  }
  50% {
    transform: translateX(-4px);
  }
  75% {
    transform: translateX(2px);
  }
  100% {
    transform: translateX(0);
  }
}

#release-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  align-self: stretch;
  margin-top: 1rem;
